import {
	renderTest,
	renderChat,
	renderVideo,

	setStreamUrl,
	SdkEventEmitter,
	ESdkControlsEvents,
} from '@vkplaylive/sdk';
import '@vkplaylive/sdk/style.css';

setStreamUrl('foobarbuz-1');
SdkEventEmitter.emit(ESdkControlsEvents.STREAM_URL_UPDATE, 'foobarbuz-1');

renderTest('#vkplaylive-test', { skin: 'theme1' });
renderChat('#vkplaylive-chat');
renderVideo('#vkplaylive-video');

document.querySelector('#button-stream-url').addEventListener('click', () => {
	const value = document.querySelector('#input-stream-url').value;

	// SdkEventEmitter.emit(ESdkControlsEvents.STREAM_URL_UPDATE, value);
	setStreamUrl(value);
});

let i = 0;
const Log = (text, data) => {
	const item = document.createElement('div');
	item.innerText = `${i++}: ${text} ${data && JSON.stringify(data)}`;
	const container = document.querySelector('#events');
	container.appendChild(item);
	container.scrollTo(0, container.scrollHeight);
};

// controls

document.querySelector('#button-start-video').addEventListener('click', () => {
	SdkEventEmitter.emit(ESdkControlsEvents.START_VIDEO);
});

document.querySelector('#button-pause-video').addEventListener('click', () => {
	SdkEventEmitter.emit(ESdkControlsEvents.PAUSE_VIDEO, { timeCode: 5545 });
});

document.querySelector('#button-like').addEventListener('click', () => {
	SdkEventEmitter.emit(ESdkControlsEvents.LIKE);
});

document.querySelector('#button-seek-video').addEventListener('click', () => {
	SdkEventEmitter.emit(ESdkControlsEvents.SEEK_VIDEO, { timeCode: 123 });
});

// events

SdkEventEmitter.addListener(ESdkControlsEvents.LIKES_COUNT, (counter) => {
	Log('Количество лайков: ', counter);
});

SdkEventEmitter.addListener(ESdkControlsEvents.VIEWERS_COUNT, (counter) => {
	Log('Количество зрителей: ', counter);
});

SdkEventEmitter.addListener(ESdkControlsEvents.STREAM_INFO_UPDATE, ({ title, category }) => {
	Log('Изменились заголовок или категория стрима: ', `${title}, ${category.title} - ${category.id}`);
});

SdkEventEmitter.addListener(ESdkControlsEvents.START_VIDEO, (data) => {
	Log('Начался стрим: ', data);
});

SdkEventEmitter.addListener(ESdkControlsEvents.END_VIDEO, (data) => {
	Log('Закончился стрим: ', data);
});

SdkEventEmitter.addListener(ESdkControlsEvents.END_VIDEO, (data) => {
	Log('Рейд на канал: ', data);
});
